#drum-machine {
    width: 100vw;
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 550px;
    height: 580px;
    background: #3dc1d3;
    border-radius: 5px;
}

.drum-pad {
    display: flex;
    width: 150px;
    height: 150px;
    justify-content: center;
	align-items: center;
    margin: 10px;
    border-radius: 5px;
    background: #c44569
}

.container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: stretch;
}

#display {
    display: flex;
    justify-content: center;
}

.App {
    height: 100vh;
    background: #303952;
    display: flex;
    justify-content: center;
    align-items: center;
}